* {
    box-sizing: border-box;
}

:host {
    display: block;
    background: red;
    padding: 2px;
    width: 70vmin;
    height: 70vmin;
}

main {
    display: block;
    background: black;
    width: 70vmin;
    height: 70vmin;
    display: flex;
    justify-content: center;
    align-items: center;

    select {
        flex: none;
        width: 90%;
        font-size: 2em;
    }
}